.single {
  padding-top: 200px;
  padding-bottom: 140px;
  &__wrap {
  }
  &__wrapper {
    padding-left: 120px;
    padding-right: 120px;
    @media (max-width: 1345px) {
      padding-right: 20px;
      padding-left: 20px;
    }
  }
  &__img {
    img {
      border-radius: 20px;
    }
  }

  &__title {
    margin-top: 115px;
    text-align: center;
    font-size: 60px;
    line-height: 70px;
    font-weight: 900;
  }

  &__info {
    margin-top: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    color: #a1a7ad;
  }

  &__date {
    margin-right: 70px;
    span {
      text-transform: uppercase;
    }
  }

  &__share {
    display: flex;
    justify-content: center;
    align-items: center;
    > ul {
      display: flex;
      list-style: none;
      margin: 0;
      padding: 0;
      > li {
        width: 25px;
        height: 25px;
        margin-left: 14px;
        &:last-child {
          margin-left: 8px;
        }
        img {
          transition: all 0.3s ease 0s;
          border-radius: 50%;
          &:hover {
            //transform: scale(1.03);
            box-shadow: 1px 1px 7px rgba(#6d7479, 0.5), -1px -1px 7px rgba(#6d7479, 0.5);
          }
        }
      }
    }
  }

  &__content {
  }

  &__questions {
  }

  &__facebook {
    display: flex;
    justify-content: center;
    > div {
      margin-top: 30px;
      padding-left: 45px;
      background: url("../img/icons/facebook.png");
      background-size: 24px 24px;
      background-position: 0 center;
      background-repeat: no-repeat;
      font-size: 16px;
      line-height: 24px;
      a {
        color: #3b5998;
        transition: all 0.3s ease 0s;
        &:hover {
          color: #1654db;
        }
      }
    }
  }
}

.content-single {
  margin-top: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__img {
    margin-right: 50px;
  }

  &__text {
    max-width: 900px;
    font-size: 20px;
    line-height: 30px;
    a {
      color: #3b5998;
      transition: all 0.3s ease 0s;
      font-size: 14px;
      &:hover {
        color: #1654db;
      }
    }

    p {
      margin-top: 0;
      margin-bottom: 40px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
.questions-single {
  margin-top: 90px;
  &__item {
    padding: 30px 35px;
    background-color: var(--colorWhite);
    border-radius: 15px;
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }

  &__name {
    font-size: 20px;
    line-height: 30px;
    font-weight: 700;
  }

  &__text {
    margin-top: 12px;
    font-size: 18px;
    line-height: 30px;
  }
}
