.contact {
  padding-top: 200px;
  padding-bottom: 180px;
  &__wrap {
  }

  &__title {
    text-align: center;
  }

  &__wrapper {
    margin-top: 90px;
    background-color: var(--colorWhite);
    border-radius: 20px;
    display: flex;
  }

  &__info {
    flex: 1 0 50%;
    padding: 90px 60px 90px 100px;
  }

  &__form {
    flex: 1 0 50%;
    padding: 100px 95px;
    background-color: var(--colorYellow);
    border-radius: 20px;
    position: relative;
  }

  &__plane {
    position: absolute;
    left: -147px;
    bottom: -55px;
  }
  &__btn {
  }
}

.info-contact {
  &__text {
    font-size: 24px;
    line-height: 30px;
    p {
      margin-top: 0;
      margin-bottom: 40px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__mail {
    margin-top: 70px;
    padding-left: 50px;
    background-image: url("../img/icons/icon-mail.png");
    background-size: 33px 24px;
    background-position: 0px 7px;
    background-repeat: no-repeat;
  }

  &__name {
    font-size: 20px;
    line-height: 30px;
    color: #9da0a3;
  }

  &__email {
    font-size: 30px;
    line-height: 36px;
    color: var(--colorMain);
    display: inline-block;
    margin-bottom: 30px;
  }

  &__phone {
    padding-left: 50px;
    background-image: url("../img/icons/icon-phone.png");
    background-size: 32px 31px;
    background-position: 0px 7px;
    background-repeat: no-repeat;
  }

  &__tel {
    font-size: 24px;
    line-height: 36px;
    color: var(--colorMain);
    margin-bottom: 30px;
    display: inline-block;
  }

  &__policy {
    padding-left: 50px;
    font-size: 20px;
    line-height: 30px;
    color: var(--colorMain);
    text-decoration: underline;
  }

  &__social {
    margin-top: 50px;
    > ul {
      list-style: none;
      padding: 0;
      margin: 0;
      display: flex;
      > li {
        margin-right: 7px;
        a {
          img {
            transition: all 0.3s ease 0s;
            border-radius: 50%;
            &:hover {
              //transform: scale(1.03);
              box-shadow: 1px 1px 5px rgba(#6d7479, 0.5), -1px -1px 7px rgba(#6d7479, 0.5);
            }
          }
        }
      }
    }
    padding-left: 50px;
  }
}
.feed-form {
  text-align: center;
  input {
    width: 100%;
    padding: 25px;
    border-radius: 10px;
    border: 0;
    margin-bottom: 32px;
    outline: none;
    &:focus {
      background-color: var(--colorBg);
    }
    &::placeholder {
      font-size: 18px;
      color: #717171;
    }
  }
  textarea {
    width: 100%;
    border-radius: 10px;
    resize: none;
    border: none;
    padding: 25px;
    height: 200px !important;
    margin-bottom: 50px;
    outline: none;
    &:focus {
      background-color: var(--colorBg);
    }
    &::placeholder {
      font-size: 18px;
      color: #717171;
    }
  }
  .btn {
    background-color: var(--colorMain);
    color: var(--colorWhite);
    border: none;
    cursor: pointer;
    &:hover {
      box-shadow: 1px 1px 7px rgba(0, 0, 0, 0.6);
    }
  }
}
